`, "",{"version":3,"sources":["webpack://./src/pages/Feedback.vue"],"names":[],"mappings":";AAiGA;EACE,6BAA6B;EAC7B,8BAA8B;AAChC;AAEA;EACE,gBAAgB;EAChB,cAAc;EACd,2BAA2B;AAC7B;AAEA;EACE,kBAAkB;EAClB,eAAe;EACf,gCAAgC;AAClC;AAEA;EACE,kBAAkB;EAClB,kCAAkC;EAClC,gCAAgC;AAClC;AAEA;EACE,WAAW;AACb;AAEA;EACE,8BAA8B;EAC9B,0BAA0B;EAC1B,mCAAmC;EACnC,yCAAyC;EACzC,+BAA+B;AACjC;AAEA;EACE,0BAA0B;EAC1B,gCAAgC;AAClC;AAEA;EACE,kCAAkC;EAClC,gCAAgC;AAClC;AAEA;EACE,cAAc;EACd,6BAA6B;EAC7B,0BAA0B;EAC1B,oCAAoC;EACpC,+BAA+B;EAC/B,kBAAkB;EAClB,gBAAgB;EAChB,0BAA0B;AAC5B","sourcesContent":["<template>\r\n  <div class=\"feedback-page\">\r\n    <div class=\"container\">\r\n      <div class=\"feedback-container card\">\r\n        <h2>意见反馈</h2>\r\n        <p class=\"subtitle\">您的反馈对我们非常重要</p>\r\n        \r\n        <el-form\r\n          ref=\"formRef\"\r\n          :model=\"form\"\r\n          :rules=\"rules\"\r\n          label-position=\"top\"\r\n          size=\"large\"\r\n          @submit.prevent=\"handleSubmit\">\r\n          <el-form-item label=\"邮箱\" prop=\"email\">\r\n            <el-input v-model=\"form.email\" placeholder=\"your@email.com\" />\r\n          </el-form-item>\r\n          \r\n          <el-form-item label=\"反馈内容\" prop=\"content\">\r\n            <el-input\r\n              v-model=\"form.content\"\r\n              type=\"textarea\"\r\n              :rows=\"8\"\r\n              placeholder=\"请详细描述您的问题或建议...\"\r\n            />\r\n          </el-form-item>\r\n          \r\n          <el-form-item>\r\n            <el-button type=\"primary\" native-type=\"submit\" :loading=\"loading\" class=\"submit-btn\">\r\n              提交反馈\r\n            </el-button>\r\n          </el-form-item>\r\n        </el-form>\r\n        \r\n        <!-- XSS 提示（场景 5 盲 XSS）：仅在 VULN 模式展示攻击示例 -->\r\n        <div class=\"demo-info\" v-if=\"configStore.xssMode === 'vuln'\">\r\n          <h3>⚠️ 盲 XSS 演示（场景 5）</h3>\r\n          <p>当前处于 VULN 模式，反馈内容将不经过滤直接存储。</p>\r\n          <p>当管理员在后台查看反馈时，恶意代码将被触发。</p>\r\n          \r\n          <p style=\"margin-top: 12px;\"><strong>基础 Payload：</strong></p>\r\n          <code>&lt;img src=x onerror=alert('Blind XSS')&gt;</code>\r\n        </div>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\n// 反馈页（场景 5）：演示盲 XSS\r\n// - 攻击者提交恶意反馈，管理员后台查看时触发\r\nimport { ref } from 'vue';\r\nimport { useRouter } from 'vue-router';\r\nimport { useConfigStore } from '@/stores/config';\r\nimport axios from '@/api/axios';\r\nimport { ElMessage } from 'element-plus';\r\n\r\nconst router = useRouter();\r\nconst configStore = useConfigStore();\r\nconst formRef = ref();\r\nconst loading = ref(false);\r\n\r\nconst form = ref({\r\n  email: '',\r\n  content: ''\r\n});\r\n\r\nconst rules = {\r\n  email: [\r\n    { required: true, message: '请输入邮箱', trigger: 'blur' },\r\n    { type: 'email', message: '请输入有效的邮箱地址', trigger: 'blur' }\r\n  ],\r\n  content: [\r\n    { required: true, message: '请输入反馈内容', trigger: 'blur' },\r\n    { min: 10, message: '反馈内容至少10个字符', trigger: 'blur' }\r\n  ]\r\n};\r\n\r\nconst handleSubmit = async () => {\r\n  const valid = await formRef.value.validate().catch(() => false);\r\n  if (!valid) return;\r\n  \r\n  loading.value = true;\r\n  try {\r\n    await axios.post('/feedback', form.value);\r\n    ElMessage.success('反馈提交成功，感谢您的宝贵意见！');\r\n    form.value = { email: '', content: '' };\r\n    formRef.value.resetFields();\r\n  } catch (error) {\r\n    ElMessage.error('提交失败，请稍后重试');\r\n  } finally {\r\n    loading.value = false;\r\n  }\r\n};\r\n</script>\r\n\r\n<style scoped>\r\n.feedback-page {\r\n  padding: var(--spacing-2xl) 0;\r\n  min-height: calc(100vh - 80px);\r\n}\r\n\r\n.feedback-container {\r\n  max-width: 800px;\r\n  margin: 0 auto;\r\n  padding: var(--spacing-2xl);\r\n}\r\n\r\n.feedback-container h2 {\r\n  text-align: center;\r\n  font-size: 2rem;\r\n  margin-bottom: var(--spacing-sm);\r\n}\r\n\r\n.subtitle {\r\n  text-align: center;\r\n  color: var(--color-text-secondary);\r\n  margin-bottom: var(--spacing-xl);\r\n}\r\n\r\n.submit-btn {\r\n  width: 100%;\r\n}\r\n\r\n.demo-info {\r\n  margin-top: var(--spacing-2xl);\r\n  padding: var(--spacing-lg);\r\n  background: rgba(251, 146, 60, 0.1);\r\n  border: 2px solid rgba(251, 146, 60, 0.3);\r\n  border-radius: var(--radius-md);\r\n}\r\n\r\n.demo-info h3 {\r\n  color: var(--color-accent);\r\n  margin-bottom: var(--spacing-md);\r\n}\r\n\r\n.demo-info p {\r\n  color: var(--color-text-secondary);\r\n  margin-bottom: var(--spacing-sm);\r\n}\r\n\r\n.demo-info code {\r\n  display: block;\r\n  margin-top: var(--spacing-sm);\r\n  padding: var(--spacing-sm);\r\n  background: var(--color-bg-elevated);\r\n  border-radius: var(--radius-sm);\r\n  font-size: 0.85rem;\r\n  overflow-x: auto;\r\n  color: var(--color-accent);\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
